// + tabContentsView
const eleTabContentsView = document.querySelector('.c-tab-list');
const tabContentsView = () => {
  $('.c-tab-list').on('click', 'li', (e) => {
    const target = $(e.currentTarget),
      tabList = target.siblings('li'),
      selectedTabLink = target.children('a'),
      selectedTab = selectedTabLink.attr('aria-controls'),
      seletedContent = $('.tab-cont').siblings(`#${selectedTab}`),
      tabContents = seletedContent.siblings('.tab-cont');

    if (selectedTabLink.hasClass('is-disable')) {
      return 0;
    } else {
      tabList.removeClass('on').find('a').attr('aria-selected', false);
      target.addClass('on').find('a').attr('aria-selected', true);

      // tabContents.hide();
      // seletedContent.show();

      tabContents.removeClass('on');
      seletedContent.addClass('on');
    }
  });
};

isContainElement(eleTabContentsView) ? tabContentsView() : false;

// + 검진항목 테이블 bg
let eleTblCheckBg = document.querySelector('.tbl-chkup-category');
const tableCheckBg = () => {
  $('.tbl-chkup-category')
    .children('tbody')
    .find('td:first-child input')
    .on('change', function () {
      // $(this).closest('tr').toggleClass('highlight', this.checked);

      let $row = $(this).closest('tr');
      let row = $row.index();
      let rowspan = $row.find('td[rowspan]').attr('rowspan') || 0;

      let $rows = $row
        .parent()
        .children()
        .slice(1, row + rowspan);
      row--;

      $rows.each(function (i) {
        let $tr = $(this);

        let rowspan = $tr.find('td[rowspan]').attr('rowspan') || 0;
        if ((i < row && rowspan + i > row) || i > row) {
          $row = $row.add($tr);
        }
      });
      if ($(this).is(':checked')) {
        $row.addClass('highlight');
      } else if (!$(this).is(':checked')) {
        $row.removeClass('highlight');
      }
    });
};

isContainElement(eleTblCheckBg) ? tableCheckBg() : false;

