// + coMemoToggle
const memoContToggle = (memoContBtn, memoContList, memoSkipTxt) => {
  Array.prototype.forEach.call(memoContBtn, (btn, index) => {
    let memeContList = btn.closest('.memo-list').querySelector('.memo-cont');
    if (memeContList.clientHeight <= 100) {
      btn.style.display = 'none';
    } else {
      btn.style.display = 'inline-block';
    }

    btn.addEventListener('click', (e) => {
      const target = e.currentTarget,
        memoList = target.closest('.memo-list'),
        contArea = memoList.querySelector('.memo-cont');

      if (contArea.clientHeight < 100) {
        return 0;
      } else {
        memoList.classList.toggle('active');

        Array.prototype.forEach.call(memoContList, (memoList, index2) => {
          if (index !== index2) {
            memoList.classList.remove('active');
          }
        });

        Array.prototype.forEach.call(memoSkipTxt, (txt, index) => {
          memoContList[index].classList.contains('active') ? (txt.innerText = '메모이력접기') : (txt.innerText = '메모이력보기');
        });
      }
    });
  });
};

