// + Form Element Align
const formElemAlign = (elem = 'default', target) => {
  let addWrapSelector = '',
    inputListSelecotor = '.c-article.input-list',
    inputListSelecotor02 = '.c-article.input-list02';

  // modal 내 input 요소 처리

  if (elem === 'default') {
    addWrapSelector = 'section.contents';
    if (document.querySelectorAll(inputListSelecotor).length > 0) {
      document.querySelector(addWrapSelector).classList.add('type-horizontal');
    }
  } else if (elem === 'modal') {
    addWrapSelector = `#${target}`;
    if (document.querySelectorAll(inputListSelecotor).length > 0 || document.querySelectorAll(inputListSelecotor02).length > 0) {
      document.querySelector(addWrapSelector).classList.add('type-horizontal');
    }
  }
};

// + 개인정보수집 약관 modal
const modalTerms = () => {
  const modalBtn = document.querySelectorAll('.c-modal-btn');
  const modalCloseBtn = document.querySelectorAll('.c-modal-wrap .c-popup-close-btn');
  const modalTool = document.querySelectorAll('.modal-tool');
  const modalWrap = document.querySelector('.c-modal-wrap');

  // modal open
  Array.prototype.forEach.call(modalBtn, (btn) => {
    btn.addEventListener('click', (e) => {
      let modalDataSet = e.currentTarget.dataset.modal;
      let modalDataValue = document.querySelector(modalDataSet);
      modalDataValue.parentNode.classList.add('open');
      modalDataValue.setAttribute('aria-hidden', false);
      if (modalTool.length > 0) {
        const btnTop = btn.getBoundingClientRect().top;
        const btnRight = btn.getBoundingClientRect().right;

        let btnLeftX = btnRight + `px`;
        let btnTopY = btnTop + `px`;

        modalWrap.style.top = btnTopY;
        modalWrap.style.left = btnLeftX;
      }
    });
  });

  // modal close
  Array.prototype.forEach.call(modalCloseBtn, (item) => {
    item.addEventListener('click', (e) => {
      let modalDataSet2 = e.currentTarget.closest('.c-modal-cont');
      let modalDataSetValue = modalDataSet2.parentNode;
      modalDataSetValue.classList.remove('open');
      modalDataSet2.setAttribute('aria-hidden', true);
      modalWrap.removeAttribute('style');
    });
  });
};

// modal tool open
const modalToolWrap = document.querySelector('.modal-tool');
const modalTool = () => {
  const modalToolOpen = document.querySelector('.modal-tool.open');
  if (modalToolOpen) {
    let modalId = document.querySelector('.modal-tool.open .c-modal-cont').id,
      dataModal = document.querySelector(`[data-modal="#${modalId}"`), // 버튼
      dataModalRect = dataModal.getBoundingClientRect(),
      btnPosY2 = dataModalRect.top,
      btnPosX3 = dataModalRect.right,
      modalIdStyle = document.querySelector(`#${modalId}`).parentNode.style;

    modalIdStyle.top = `${btnPosY2}px`;
    modalIdStyle.left = `${btnPosX3}px`;
  } else {
    return 0;
  }
};

isContainElement(modalToolWrap) ? modalTool() : false;

window.addEventListener('resize', () => {
  modalTool();
});

